<template>
  <div>
    <!-- 外壳 -->
      <el-card class='box-card box-card-ex'>
        <!-- <div slot='header' class='clearfix' style='text-align: left'>
        </div> -->
        <!-- 内容 -->
        <el-table
        :data="tableMsg"
        style="width: 100%;padding:10px 20px">
        <!-- <el-table-column
        prop="id"
        label="序号"
        width='100'>
      </el-table-column> -->
        <el-table-column
          prop="parkingName"
          label="车场名称">
        </el-table-column>
        <el-table-column
          prop="plateNum"
          label="车牌号">
        </el-table-column>
        <el-table-column
          prop="plateColor"
          label="车牌颜色">
        </el-table-column>
        <el-table-column
          prop="passingTime"
          label="过车时间">
        </el-table-column>
        <el-table-column
          prop="inOrOut"
          label="进出类型">
        </el-table-column>
        <el-table-column
          prop="recognition_type"
          label="识别类型">
        </el-table-column>
        <el-table-column
          prop="paymentTollFee"
          label="消费金额">
        </el-table-column>
        <!-- 
        <el-table-column
        label="操作" width="260px">
        <template slot-scope="scope">
            <el-button  
                @click="handleDetailMsg(scope.$index, scope.row,scope)"
                type="primary"
                size="small">详情
            </el-button>
            <el-button
                @click="handleChangeMsg(scope.$index, scope.row,scope)"
                type="primary"
                size="small">编辑
            </el-button>
        </template>
        </el-table-column>
        -->
      </el-table> 
      </el-card>
  </div>
</template>
<script>
  import moment from 'moment'
  export default{
    data() {
      return {
       //数据
       tableMsg:[],

      }
    },
    methods: {
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      //定义的方法  
      openSocket(){
        var self =this;
        self.ws=new WebSocket("ws://"+process.env.WEB_SOCKET);
        self.ws.onopen=function(event){
        }
        //接收消息  aresJSON();- string->object   . unshift()
          self.ws.onmessage=function(msg){
            var tempArr=(JSON.parse(msg.data).data);
            if(tempArr.recognition_type=='0'){
              tempArr.recognition_type='rfid'
            }else if(tempArr.recognition_type=='1'){
              tempArr.recognition_type='车牌识别'
            }else{
              tempArr.recognition_type='两者均有'
            }

              self.tableMsg.unshift({
                'parkingName':tempArr.parkingName,
                "plateNum":tempArr.plateNum,
                "plateColor":tempArr.plateColor,
                "passingTime":self.momentTime(tempArr.passingTime),
                "inOrOut":tempArr.inOrOut,
                "paymentTollFee":!tempArr.paymentTollFee?tempArr.paymentTollFee="0":tempArr.paymentTollFee=Math.ceil(tempArr.paymentTollFee/100),
                'recognition_type':tempArr.recognition_type
              });
          }
      }
    
    },
    // 钩子函数
    mounted:function(){
      var self=this;
      this.openSocket();
      // self.$forceUpdate();
    }
  }
</script>

<style scoped>
.el-checkbox{
  margin-left:8px !important;
}
.clearfix:before,
.clearfix:after{
  display:table;
  content:"";
}
.clearfix:after{
  clear:both;
}
.box-card-ex{
  margin-top:15px !important;
}
.handle-input{
  width:140px;
  display: inline-block;
}
.laeblStyle{
  width:220px;
  text-align:right
}
.timeStyle{
  width:300px;
  text-align:right;
}
.handle-date{
  width:150px;
}
</style>



